.comboBoxDiv{
padding: 10px 0px;
margin:0;
border:none;
width: 100%;
font-family: Roboto,Arial,Helvetica,sans-serif;    

}
.comboBoxDiv p{
    font-size: 15px;
    margin: 0;
    padding: 1px 0px;
    color:#3d3d3d;

}
.comboBox{
    padding: 13px;
    width: 55%;
    margin: 0;
    border: 1px solid rgba(0,0,0,0.4);
    border-radius: 5px;
    outline:none;
}
.comboBoxName{
    font-size: 14px!important;
}
.comboBoxOption{
    padding: 13px 0px!important;
}
.Red{
    border: 2px solid rgb(251, 5, 5);
}
.colorRed{
    color:  rgb(251, 5, 5)!important;
    font-size: 13px!important;
}