.belowTop{
    margin: 0px 100px;
    display:flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

.centerdiv{    
    border: 1px solid rgba(0,0,0,0.2);
    border-radius: 3px;
    width: 80%;
    align-self: center;
}
.belowTop .postYourAdHead{
text-align: center;
padding: 15px 10px;
width: 100%;
color: #3e3e3e;
font-weight: 600;
}
.inneraboveCenterDiv{
    padding:10px;
    border-bottom: 1px solid rgba(0,0,0,0.2);
}
    
.inneraboveCenterDiv span{
    display: inline-block;
    width: 10%;
}
/* .inneraboveCenterDiv h5 ~ div {
clear: both;
} */
.inneraboveCenterDiv h5{
padding: 0;
margin: 10px 0px;
}
.ChangeBtn{
    padding: 5px!important;
}
.innerbelowCenterDiv{
    padding: 20px;
    font-family: Roboto,Arial,Helvetica,sans-serif;    
}
.innerbelowCenterDiv h5{
font-weight: bolder;
}




@media(max-width:px){
    .centerdiv{
        width: 100%;
    }
}