.top{
    padding: 10px 0px;
    background-color: #F5F5F5;
    box-shadow: 0px 0px 3px rgba(0,0,0,0.2);
}
.top i{
    color:#2e2e2e!important;
margin-right:30px;
}
.top h2{
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
color:#2e2e2e;
}


.belowTop h3{
text-align: center;
padding: 10px;

}

.centerDiv{
display: flex;
justify-content: center;
flex-wrap: wrap;

}
.listUpper-UpperDiv h5{
    width: 100%;
    font-size: 17px;
    font-weight: bolder;
    padding: 25px 30px 20px 30px;
}
.listUpper-UpperDiv{
    width:70%;
    border-radius: 3px;
    border: 1px solid rgba(0,0,0,0.2);
    position: absolute;
}
/* .listUpperDiv{
    border: 1px solid rgba(0,0,0,0.2);
} */
.categoriesList{
    padding:0;
    width: 50%;    
    /* border: 1px solid #000; */
    float: left;
}
.SubCategoriesList{
    display: flex;
    justify-content:stretch;
    flex-wrap: wrap;
    position: relative;
    padding: 0;
    margin: 0;
    top: 0;
    bottom: auto;
    width:50%;
    /* border: 1px solid red; */

}
.categoriesList li,.SubCategoriesList .noDecor li{
    
    border-top: 1px solid rgba(0,0,0,0.2);
    border-right: 1px solid rgba(0,0,0,0.2);
width: 100%;
line-height: 50px;
padding: 2px;
color:#455A64;
padding-left:10px;
font-size: 17px;
}
.noDecor{
    display: flex;
    justify-content: stretch;
    flex-wrap: wrap;
    flex-grow: 1;
}
.SubCategoriesList li{
    
    padding-left:20px;
    margin:0;
    border-right: none;
    /* border: 1px solid #000; */
}
.categoriesList li:hover i,.categoriesList li:hover{
    background-color: rgb(190, 204, 211);
color:#263238;
cursor: pointer;
}
.categoriesList li :active {
    background-color: rgb(190, 204, 211);
    color:#263238;
}

.categoriesList li .arrow{
    color: #546E7A;
    line-height: 50px;
    float:right;
    margin-right:5px;
}
.ImageIcon{
    padding:5px;
    margin-right:10px;
}
#li0{
    border:none;
}