.buttonDiv{
padding: 0;
display: flex;
flex-wrap: wrap;
justify-content: space-around;
width:50%;
}

p{
    font-size: 15px;
    margin: 0;
    padding:0;
    color:#212121;
}
.buttonDiv button{
    outline: none;
    margin: 3px 0px;
}

#btn0,#btn1,#btn2,#btn3,#btn4{
    color:#00a49f !important;
    font-weight: bolder!important;
    outline:none;
    border:2px solid #00a49f !important; 
    background-color:#c8f8f6 !important;
    border-color:#00a49f !important;
}
.buttonDiv button:hover{
align-self: flex-start;
background-color:#c8f8f6 !important;
border-color:#00a49f !important;
color:#00a49f;
}