.FooterSection{

    padding:40px 0px;
    background-color: #ECEFF1;
    border :none;
    border-top:1px solid rgba(0, 0, 0, 0.1);
    display: block;
}
.FooterSection h6{

    font-size: 15px;
    font-weight: 700;
    color: #263238;
}
.links{
    text-align: left;
    padding:0px;
    font-size: 12px;
    font-family: 'Roboto-Regular', sans-serif;
    color: #546E7A;
}
.links:hover{
    color: #263238;
    text-decoration: none;
}
.popularList{
    display: block;
    padding: 4px 0px;
    margin: 0;
}
.popularList li{
    display: flex;
    justify-content: start;
    align-items: center;
}
.footerRow{
    background-color: #ECEFF1!important;   
}
.innerColDiv{
    padding:0px 20px;
}

.followInnerColDiv{
    display: flex;
    justify-content: start;
}
.followInnerColDiv a{
    height: 25px;
    width: 25px;
    margin: 4px;
    margin-left:0px;
    padding: 5px;
    border: 0.06em solid #546E7A;
    border-radius: 50%;
}

/* .socialSpan{
   
   
    
} */

.socialSpan i{
    color:#546E7A;
}
#Icon0{
    transform: translate(-5%,-28%);
}
#Icon1,#Icon2,#Icon3{
    transform: translate(-3%,-29%);
}

.AppdownloadLinks{
    display: flex;
    justify-content: stretch;

}
.AppdownloadLinks span a{
    border: none;
}
.AppdownloadLinks span img{
    width: 100px;
    height: auto;
    margin: 5px;

}

/* ******************** BELOW FOOTER ************************ */
.belowFooter{

    background-color:rgb(0, 51, 53);
}
.belowFooter pre{
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    color:whitesmoke;
    margin: 0;
    font-size: 13px;
    padding: 15px 0px;
}
.belowFooter pre span{
    font-weight: bold;
}
.inlineBlock{
    display: inline-block;
}
.float-left{
    float: left;
}
